.form {
  display: block;
  position: relative;
  float: left;
  width: 100%;
  &__img {
    display: block;
    position: relative;
    float: left;
    width: 100%;
    height: 415px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content {
    z-index: 999;
    width: 100%;
    display: block;
    position: absolute;
    top: 0;
  }
  h1 {
    font-size: 45px;
    text-transform: uppercase;
    font-family: $MuSeoSansBlack;
    color: #ffffff;
    margin-top: 75px;
    margin-bottom: 24px;
    span {
      color: #edc900;
    }
  }
  p {
    width: 661px;
    height: 65px;
    margin: 0;
    color: #ffffff;
    font-size: 30px;
    font-weight: 500;
  }
  button {
    display: inline-block;
    float: left;
    width: 287px;
    height: 57px;
    border-radius: 10px;
    background-color: #edc900;
    border: 0;
    margin-right: 22px;
    margin-top: 58px;
    margin-bottom: 83px;
    color: #000000;
    font-family: $MuSeoSansBold;
    font-size: 19px;
    &:nth-of-type(2) {
      background: transparent;
      border: 2px solid #ffffff;
      color: #fff;
    }
  }
  &__absolute {
    position: absolute;
    top: 0;
    right: 20px;
  }
}